@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Space+Grotesk:wght@300..700&display=swap');


.Home-heading {
    color: var(--Black-100, #03061F);
    text-align: center;
    font-family: Poppins;
    font-size: 50px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
}

strong {
    color: #d32f2f;
}


/*Home peragraph */
.custm-pera {
color: var(--Black-100, #03061F);
text-align: center;
font-family: "Space Grotesk";
font-size: 21px;
font-style: normal;
font-weight: 400;
line-height: normal;
}




/* app link */
.links img {
    width: 9rem;
    -webkit-transition: width 0.3s ease;
    -o-transition: width 0.3s ease;
    transition: width 0.3s ease;
}

.app-links:hover{
    color: black;
    
}

.applinkMargin{
    margin-left: 7%;
    margin-top: 3rem;

}


.side-icons{
    margin-top: 0;
}

.icon-right{
    margin-top: 0;
}

/* player piks*/

.player-list ul {
    list-style-type: none;

}

.player-list ul li {
    display: inline;
    
}

.player-list ul li img {
    width: 100%;
    margin: -5rem 1rem;
}


.player-padding {
    padding-bottom: 12rem !important;
}

.ball{
    width: 80% !important;
    margin-left: 3rem !important;
}

@media screen and (max-width: 1284px) {
    /* Your CSS styles for iPhone 14 Pro */
    .leftPlay, .rightPlay {
        display: none;
    }
    .animation1{
        display: none;
    }

    .animation2{
        display: none;
    }

    .player2, .player3 {
        width:  8rem !important;
        margin-top: 3rem !important;
    }

    .side-icons {
        margin-top: 1rem !important;
    }

    .icon-right {
        margin-top: 4rem !important;
    }

    .ball {
        display: none;
    }

    .player-padding {
        padding-bottom: 0 !important;
    }

    .player-list ul li img {
        margin: 0;
    }
}

/* iPhone 14 Pro in portrait mode */
@media only screen 
  and (min-device-width: 1284px) 
  and (max-device-width: 1284px) 
  and (-webkit-min-device-pixel-ratio: 3)
  and (orientation: portrait) {
    .leftPlay, .rightPlay {
        display: none;
    }
    .animation1{
        display: none;
    }

    .player2, .player3 {
        width:  8rem !important;
    }

    .side-icons {
        margin-top: 1rem !important;
    }

    .icon-right {
        margin-top: 4rem !important;
    }

    .ball {
        display: none;
    }

    .player-padding {
        padding-bottom: 0 !important;
    }

    .player-list ul li img {
        margin: 0;
    }
}

/* iPhone 14 Pro in landscape mode */
@media only screen 
  and (min-device-width: 2778px) 
  and (max-device-width: 2778px) 
  and (-webkit-min-device-pixel-ratio: 3)
  and (orientation: landscape) {
    .leftPlay, .rightPlay {
        display: none;
    }
    .animation1{
        display: none;
    }

    .player2, .player3 {
        width:  8rem !important;
    }

    .side-icons {
        margin-top: 1rem !important;
    }

    .icon-right {
        margin-top: 4rem !important;
    }

    .ball {
        display: none;
    }

    .player-padding {
        padding-bottom: 0 !important;
    }

    .player-list ul li img {
        margin: 0;
    }
}



