.screenshots {
  background-color: #080F41 ;

}

.screenShots-info {
  width: 80%;
}

.screenShots-info p {
  font-size: 12px;

}

/* Slider.css */

.cover {
  position: relative;
  width: 100%;
  padding: 4rem 0;

}

.backgoundImg {

  position: absolute;
  width: 24%;
  height: auto;
  top: 6%;
  left: 38%;
  -o-object-fit: cover;
     object-fit: cover;
  z-index: 0;
}


.swiper-container {
  z-index: 1;

}

