.swiper-slide img {
  width: 100%;
}

.swiper-container {
  width: 80%; /* Adjust the value as needed */
  margin: 0 auto; /* Center the container horizontally */
}



@media screen and (max-width: 1284px) {


  .screenShots-info {
  width: 100%;
}
}



